import React, { ReactElement } from 'react';

class ReduxFieldRender {
  
  public renderTextField(metaData: any): ReactElement {
    const { 
      input, 
      placeholder, 
      type, 
      onKeyUp,
      className,
      meta: { 
        error, 
        warning 
      } 
    } = metaData;

    return (
      <>
        <input
          { ...input } 
          className={ className } 
          placeholder={ placeholder } 
          type={ type }
          onKeyUp={ onKeyUp }
        />
        {
          (error && <span className="text-danger">{ error }</span>) 
          || 
          (warning && <span className="text-warning">{ warning }</span>)
        }
      </>
    );
  }

  public renderTextArea(metaData: any): ReactElement {
    const { 
      input, 
      placeholder,
      type,
      className,
      meta: {  
        error, 
        warning 
      } 
    } = metaData;
  
    return (
      <>
        <textarea
          { ...input } 
          className={ className } 
          placeholder={ placeholder } 
          type={ type }
        />
        {
          (error && <span className="text-danger">{ error }</span>) 
          || 
          (warning && <span className="text-warning">{ warning }</span>)
        }
      </>
    );
  }

  public renderCheckBox(metaData: any): ReactElement {
    const { 
      input, 
      label, 
      type,
      className, 
      meta: { 
        error, 
        warning 
      } 
    } = metaData;
    
    console.log(error)
    return (
      <>
        <input
          { ...input }
          className= { className }
          placeholder={ label } 
          type={ type }
        />
        <label>&nbsp;&nbsp;&nbsp;&nbsp;{ label }</label>
        {
          (error && <div className="text-danger">{ error }</div>) 
          || 
          (warning && <span className="text-warning">{ warning }</span>)
        }
      </>
    );
  }

}

export default ReduxFieldRender;