export class MaterialModel {

  name: string;
  description: string;
  img: string;
  uses: Array<boolean>;
  value: string;
  life: number;

  constructor(data: any | MaterialModel) {
    this.name = '';
    this.description = '';
    this.img = '';
    this.uses = [];
    this.value = '';
    this.life = 0;
    this.uses = [
      false,
      false, 
      false, 
      false, 
      false, 
      false,
      false,
      false
    ];
    Object.assign(this, data);
  }
}