import React, { Component } from 'react'
import { Field, reduxForm, connect, FieldArray } from '../../../../imports/react-redux.import'
import { Modal, Button, Col } from 'react-bootstrap';
import { MaterialModel } from '../../../../core/models/material.model';
import { FormEditMaterialInterface } from '../../../../core/interfaces/material-component.interface';
import { ExampleEnum } from '../../../../core/enums/material-reducer.enum';
import { updateMaterials } from '../../../../core/actions/material.actions';
import LifeIndicator from '../../../../shared/life-indicator.shared';
import ReduxFieldRender from '../../../../shared/redux-field-render.shared';
import { MaterialUses } from '../../../../core/constants/material-uses.constants';

const globalValues = {
  renderCheckBox: new Function(),
  keyChecks: -1 
}

class FormEditMaterialComponent extends Component<FormEditMaterialInterface,any> {

  lifeIndicator: LifeIndicator;
  reduxFieldRender: ReduxFieldRender;

  constructor(props: any) {
    super(props);

    this.state = {
      show: false,
      life: 0,
      img: ''
    };
  
    this.lifeIndicator = new LifeIndicator();
    this.reduxFieldRender = new ReduxFieldRender();

    globalValues.renderCheckBox = this.reduxFieldRender.renderCheckBox;
  }



  //ACTIONS
  private openModal(): void {
    this.setState({ 
      show: true,
      life: this.props.initialValues.life,
      img: this.props.initialValues.img
    });

    this.props.initialize(this.props.initialValues); 
    console.log(this.props.initialValues);
  }

  private closeModal(reset: Function): void {
    this.showModal(false);
    reset();
  }

  private showModal(show: boolean): void {
    this.setState({ 
      show: show 
    }); 
  }

  private heartDemo(evt: any): void {
    this.setState({
      life: evt.currentTarget.value
    });
  }

  private iconDemo(evt: any): void {
    this.setState({ 
      img: evt.currentTarget.value 
    });
  }

  private onSubmitedActions(valid: boolean): void {
    if (valid) {
      this.showModal(false);
    }
  }

  private submit(values: any, reduxFuction: any, reduxForm: any): void {
    const { reset, updateMaterials, id } = reduxForm;
    console.log(values);
    updateMaterials(id, values);
    reset();
  }



  //RENDER 
  private renderCheckBoxes(metaData: any) {
    const { 
      fields,
      className, 
      meta: { 
        error,
        warning 
      } 
    } = metaData;

    console.log(error);

    return(
      <Col className={ className } md={ 12 } >
        {
          fields.map((uses: any, index: number) => {
            globalValues.keyChecks++;
            return (
              <>
                <Field 
                  key= { `checkField${ globalValues.keyChecks }` }
                  name={ uses } 
                  type="checkbox" 
                  component={ globalValues.renderCheckBox } 
                  label={ MaterialUses[index] }
                  value={ true }
                />
                <br />
              </>
            );
          })
        }
        {
          (error && <span className="text-danger">{ error }</span>) 
          || 
          (warning && <span className="text-warning">{ warning }</span>)
        }
      </Col>
    ); 
  }

  




  render() {
    const { handleSubmit, reset, submitting, valid } = this.props;

    return (
      <>
        <Button 
          variant="outline-info" 
          onClick={ () => this.openModal() }
        >
          Editar
        </Button>

        <Modal show={ this.state.show }>
          <Modal.Header>
            <Modal.Title>
              Editar Material
            </Modal.Title>
          </Modal.Header>

          <form onSubmit={ handleSubmit(this.submit) }>
            <Modal.Body>
              
              <Col md={ 12 } className="text-center mb-3">
                <img 
                  alt={ this.props.initialValues.name }
                  src={ this.state.img }
                  width="40" 
                  height="40"
                />
              </Col>

              <Field 
                className="form-control mb-3"
                name="img" 
                type="text" 
                component={ this.reduxFieldRender.renderTextField } 
                label="Icono"
                onKeyUp={ (evt: any) => this.iconDemo(evt) }
              />
              
              <FieldArray 
                className="mb-3"
                name="uses" 
                component={ this.renderCheckBoxes }
              />

              <Field 
                className="form-control mb-3"
                name="name" 
                type="text" 
                component={ this.reduxFieldRender.renderTextField } 
                label="Nombre"
              />    

              <Field 
                className="form-control mb-3"
                name="description" 
                type="textarea" 
                component={ this.reduxFieldRender.renderTextArea }
              />

              <Field 
                className="form-control mb-3"
                name="value" 
                type="number" 
                component={ this.reduxFieldRender.renderTextField } 
                label="Valor en Rupias"
              />  

              <Field 
                className="form-control mb-3"
                name="life" 
                type="number" 
                component={ this.reduxFieldRender.renderTextField } 
                label="Vida"
                onKeyUp={ (evt: any) => this.heartDemo(evt) }
              />

              <Col md= { 8 }>
                { this.lifeIndicator.heartSymbol(this.state.life) }
              </Col>
            </Modal.Body>

            <Modal.Footer>
              <div>
                <Button 
                  variant="outline-info"
                  type="submit" 
                  disabled={ submitting }
                  onClick={ () => this.onSubmitedActions(valid) } 
                >
                  Guardar
                </Button>

                <Button 
                  className="ml-3"
                  variant="outline-danger"
                  type="button" 
                  onClick={ () => this.closeModal(reset) }
                >
                  Cancelar
                </Button>
              </div>
            </Modal.Footer>
          </form>
        </Modal>
      </>
    );
  }

}

const validate = (values: any) => {
  const errors = {
    name: '',
    description: '',
    life: '',
    uses: { 
      _error: '' 
    }
  }

  if (!values.name) {
    errors.name = 'El nombre es requerido';
  } 

  if (!values.description) {
    errors.description = 'La descripcion es requerida';
  } 

  if (!Number.isNaN(values.life)) {
    if (values.life > 30) {
      errors.life = 'El maximo de corazones es 30';
    } 

    if (values.life < 0) {
      errors.life = 'No puede haber corazones negativos';
    }
  }

  if (Array.isArray(values.uses)) {
    const uses = values.uses; 
    if (!(
      uses[0] ||
      uses[1] ||
      uses[2] ||
      uses[3] ||
      uses[4] ||
      uses[5]
    )) {
      errors.uses._error = 'El material debe tener una propiedad'
    }
  }
  
  return errors
}

const warn = (values: any) => {
  const warnings = {}
  return warnings
}

const mapDispatchToProps = (dispatch: Function) => ({
  updateMaterials: (id: number, data: MaterialModel) => ( dispatch(updateMaterials(id, data)) )
});

const FormEditMaterialComponentConnect = connect(null,mapDispatchToProps)(
reduxForm({
  form: ExampleEnum.SUBMIT_EDIT_MATERIAL_FORM,
  validate,
  warn
})(FormEditMaterialComponent));
export default FormEditMaterialComponentConnect;